.profile-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 5vh;
  border: 1px solid black;
  cursor: pointer;
  & * {
    font-size: 1.2em;
    transition: all 0.5s;
  }
  & * ~ * {
    margin-top: 0.5vh;
  }
}
